import React from 'react';
import {inject, observer} from 'mobx-react';
import {Form, Row, Col, Card, Steps, Icon, Tooltip, Input} from 'antd';
import moment from 'moment';

import EditableTable2 from "@/components/product/EditableTable2";
import {message} from "antd/lib/index";
//import MyUpload from "@/components/worktile/MyUpload";
import * as utils from "@/utils/utils";
import * as mobx from "mobx";
const dateFormat = 'YYYY-MM-DD';

@observer
export default class StepInfo2 extends React.Component {
	constructor(props) {
        super(props);
        this.store = this.props.store;
        this.state = {
            option:null,
            current: 0,
            isEdit:false,
            data:this.props.data,
            defaultTree: {}, // 记录默认数据
        }
    }


    componentWillReceiveProps(nextProps) {
        if(nextProps.data != this.props.data) {
           this.setState({data:nextProps.data},()=>{
               let stepChildren = nextProps.data.children!=null || typeof(nextProps.data.children)!=="undefined" ? nextProps.data.children:[];
               // 更新记录当前树的附件数据
               let attc = stepChildren.filter((item)=>{return item.treeType==='attc'})[0]
               if (Object.keys(this.state.defaultTree).length>0&&attc!==undefined) {
                   // 更新tree
                   let defaultTree = JSON.parse(JSON.stringify(this.state.defaultTree));
                   this.setState({
                       defaultTree: utils.updateTreeData(defaultTree, "update", attc)
                   });
               }
           })
        }
    }

    // 保存步骤
    saveStep =() => { //这里调用saveNodes
       const {saveNodes} = this.props;
       const {data} = this.state;
        // 校验1：任务名称必输
        let currentDataSource = data.children!=null&&data.children[0].children!=null?data.children[0].children:[];
        const isSub = currentDataSource.filter((item)=>{return item.name==""||item.name==null})
        if (isSub.length>0) {  // true
            message.warn("任务名称是必填项");
            return false;
        }
       saveNodes(data);
       this.onChangeVis();
    }

    // 更新该节点树 数据
    saveData = (taskdata, key) => {
        const {data} = this.state;

        let newData = [];
        taskdata.forEach(item=>{
            let {key, children, ...obj} = item;
            //if(obj.endTime!=0)obj.endTime = moment(obj.endTime, dateFormat).valueOf();
            newData.push(obj);
        })


        let currentTaskC = data.children.filter(item=> item.treeType==='taskc')[0];
        currentTaskC = Object.assign(currentTaskC,{"children":taskdata})

        let stepChildren = data.children;
        const taskcIndex = stepChildren.findIndex(item=> {return item.treeType==='taskc'} )
        stepChildren[taskcIndex] = currentTaskC;
        // console.log(Object.assign(data, {"children":stepChildren}))
        this.setState({data:Object.assign(data, {"children":stepChildren})});

        // saveTasks(newData);
    }

    onChangeVis = () => {
        this.setState({
            isEdit: !this.state.isEdit
        }, ()=>{
            // 点击编辑，记录当前树
            if (this.state.isEdit==true) {
                this.setState({
                    defaultTree: Object.assign({}, this.state.data)
                }, ()=> {
                    // 同步大阶段的保存状态
                    this.props.updatedeCount(true);
                });
            } else { // 取消
                // 更新tree
                let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                this.store.treeData = utils.updateTreeData(treeData, "update", this.state.defaultTree);
                // 同步大阶段的保存状态
                this.props.updatedeCount(false);
            }
        });
    }

    render() {
        let _this = this;
        const { data, isEdit, current } = _this.state;
        const  {stepName, updateTree, disabled, oss} = _this.props;
        let currentDataSource = data.children!=null&&data.children[0].children!=null?data.children[0].children:[];
        let expandedKeys = mobx.toJS(_this.store.expandedKeys)[data.id]!== undefined?mobx.toJS(_this.store.expandedKeys)[data.id]:[]
        let isExp = expandedKeys.length>0?true:false;
		return (
            <div className={"mb15"}>
                <div className={"mb15"}>
                    <span className="mr20">{stepName}</span>
                    {!isEdit ?
                    <span>
                        {/*{!disabled?:null}*/}
                        <Tooltip placement="bottom" title={"编辑"}>
                            <Icon
                                onClick={_this.onChangeVis}
                                style={{fontSize:'20px', marginRight:'10px'}}
                                type="form" />
                        </Tooltip>
                    </span>
                    :
                    <span>
                        <Tooltip placement="bottom" title={"取消"}>
                            <Icon
                                onClick={this.onChangeVis}
                                style={{fontSize:'20px', marginRight:'10px'}}
                                type="close-circle" />
                        </Tooltip>
                        <Tooltip placement="bottom" title={"保存"}>
                            {isExp? <Icon
                                style={{fontSize:'20px', color:"#e6e6e6"}}
                                type="check-circle" />: <Icon
                                onClick={_this.saveStep}
                                style={{fontSize:'20px'}}
                                type="check-circle" />}
                        </Tooltip>
                    </span>
                    }
                </div>
                <EditableTable2
                    id={data.id}
                    store={_this.store}
                    masters={data.masters==null?[]:data.masters}
                    dataSource={currentDataSource}
                    oss={this.props.oss}
                    taskhaveattt={true}
                    saveData={_this.saveData}
                    disabled={!isEdit}
                    saveAttachment={this.props.saveAttachment}
                    delNode={this.props.delNode}
                    projectId={data.projectId}
                    pid={data.children!=null ? data.children[0]['id']:''}
                />
            </div>

		)
	}
}




