/*
* 项目信息组件
* */
import React from 'react';
import {Input, Form, Row, Col, Card, Button} from 'antd';

import AsyncSelectMuti from "@/components/common/AsyncSelectMuti";
import authSchemas from "@/utils/Schemas/authorization";
import Config from "@/config";
import Schemas from "@/utils/Schemas/bpm";
import MyUpload from './MyUpload';
import PersonAddIcon from "@/components/worktile/PersonAddIcon";

const {TextArea} = Input;

@Form.create()
export default class Index extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.store;
		this.state = {
			userIds:[],
            fields: {
                "id": "",
                "pid": "",
                "projectId": "",
                "treeType": "projectinfo",
                "masters": null,
                "code": null,
                "name": "",
                "customer": null,
                "productType": null,
                "address": null,
                "description": null,
                "milepost": null,
                "template": 0,
                "currentTaskState": 0,
                "currentProgress": 0,
                "state": 0,
                "stageId": 0,
            },  // 存储当前组件的数据
            hidden: true,  // 面板折叠展开
		};
	}

    componentWillReceiveProps(nextProps,nextContext){
        const {itemObj} = nextProps;
        if (JSON.stringify(itemObj)!=JSON.stringify(this.props.itemObj)) {
            this.setState({
                fields: Object.assign({}, itemObj.children.filter((item)=>{ return item.treeType=="projectinfo"})[0]),
                userIds: itemObj.children.filter((item)=>{ return item.treeType=="projectinfo"})[0].masters==null?[]:itemObj.children.filter((item)=>{ return item.treeType=="projectinfo"})[0].masters,
            });
        }
    }

    componentDidMount() {

    }

	// handleSelChange = (val, e) => {
	// 	let userIds;
	// 	if (e.length) {
	// 		userIds = e.map((item)=>{return {userId: item.props.value, userName: item.props.children}})
	// 	} else {
	// 		userIds  = []
	// 	}
	// 	this.setState({
	// 		userIds
	// 	});
	// }

	// 保存
	handleSubmit = (e) => {
		e.preventDefault();
		let _this = this;
		const {userIds} = _this.state;
        const {projectId, projectType} = this.props;
        _this.props.form.validateFields((err, values) => {
			if (!err) {
				const params = Object.assign(values, {"id": projectId, "masters":userIds, "projectType":projectType, "productType": values.productType.map((item)=> item.toString())});
				_this.props.saveProject(params);
			}
		});
	}

	onChangeVis = () => {
        this.setState({
            hidden: !this.state.hidden
        });
    }

    // 取消
    onCanlc = ()=> {
        if (this.props.isShow) {   // 展开未保存清空
            this.props.updateStateItem("item", {isEdit: true});
            this.props.form.resetFields();
            this.setState({
                userIds: []
            });
        } else {  // 展开已保存清空
            this.props.form.resetFields();
            this.props.updateStateItem("item", {isEdit: true});
            // 清空数据
            this.props.updateTree();
        }
    }

    onChangeEdit = () => {
	    this.props.updateStateItem("item", {isEdit: false});
    }

    // 人员选择更新
    updatePerson = (userIds)=>{
        this.setState({
            userIds: userIds,
        });
    }

	render() {
		const {userIds, hidden, fields} = this.state;
		const {getFieldDecorator} = this.props.form;
		const formItemLayout = {
			labelCol: {
				xs: {span: 24},
				sm: {span: 6},
			},
			wrapperCol: {
				xs: {span: 24},
				sm: {span: 16},
			},
		};
		const {itemObj, isEdit} = this.props;
        let attcObj = itemObj.children!=null&&itemObj.children.filter((item)=>{return item.treeType==='attc'})[0];  //附件对象
        const attachments = attcObj.children!=null?attcObj.children.map((vitem) => {
            return Object.assign({}, {
                uid: vitem.id,
                name: vitem.fileName,
                status: 'done',
                url: vitem.filePath,
                fileId: vitem.fileId,
                fileName:vitem.fileName,
                filePath: vitem.filePath,
                id: vitem.id
            })
        }) : [];

		return (
			<Card bordered className="pr" style={{width: '100%'}}>
				<h3 id="items0" className="kanban_title mb20">项目信息</h3>
				<span className="worktile_progress_add">
                    {hidden?
                        <Button type="link" onClick={this.onChangeVis}>创建</Button>:
                        isEdit? <Button type="link" onClick={this.onChangeEdit}>编辑</Button>:
                        <div>
                            <span onClick={this.onCanlc} className="mr10">取消</span>
                            <span onClick={this.handleSubmit}>保存</span>
                        </div>
                    }
                </span>
				<Form className={hidden?"hidden":""} layout="inline" {...formItemLayout}>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="项目名称" className="w">
								{getFieldDecorator('name', {
									initialValue: fields.name||"",
									rules: [{required: true, message: '请输入项目名称'}],
								})(
									<Input placeholder="请输入" disabled={isEdit} className="w"/>
								)}
							</Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
                            <Form.Item label="主负责人" className="w">
                                {getFieldDecorator('masters', {
                                    //initialValue: itemObj.masters==null?[]:itemObj.masters,
                                    rules: [{required: false, message: '请输入主负责人'}],
                                })(
                                    <PersonAddIcon
                                        data={userIds}
                                        updatePerson={this.updatePerson}
                                        disabled={isEdit}
                                    />
                                )}
                            </Form.Item>
						</Col>
						<Col xs={24} sm={24} md={18} lg={12} xl={8}>
							<Form.Item label="客户名称" className="w">
								{getFieldDecorator('customer', {
									initialValue: fields.customer||"",
									rules: [{required: false, message: '请输入客户名称'}],
								})(
									<Input placeholder="请输入" disabled={isEdit} className="w"/>
								)}
							</Form.Item>
						</Col>
					</Row>
					<Row gutter={[{xs: 24, sm: 24, md: 18, lg: 12, xl: 8}, 15]} style={{margin:"0"}}>
						<Col xl={16}>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="产品类型" className="w">
									{getFieldDecorator('productType', {
										initialValue: fields.productType==null?[]:fields.productType,
										rules: [{required: false, message: '请选择产品类型'}],
									})(
										<AsyncSelectMuti
											className="w"
											type="getProductTypeList"
											variables={{}}
											query={Schemas.bpm.getProductTypeList}
											url={Config.bpm.getProductTypeList}
											value={fields.productType==null?[]:fields.productType}
											itemKey="id"
											itemValue="typeName"
                                            disabled={isEdit}
										/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={12} xl={12}>
								<Form.Item label="项目地点"  className="w">
									{getFieldDecorator('address', {
										initialValue: fields.address||"",
										rules: [{required: false, message: '请输入项目地点'}],
									})(
										<Input placeholder="请输入" disabled={isEdit} className="w"/>
									)}
								</Form.Item>
							</Col>
							<Col xs={24} sm={24} md={18} lg={16} xl={24}>
								<Form.Item
									wrapperCol={{span: 20}}
									labelCol={{
										xs: {span: 24},
										sm: {span: 3},
									}}
									label="关键里程碑" className="w">
									{getFieldDecorator('milepost', {
										initialValue: fields.milepost||"",
										rules: [{required: false, message: '请输入关键里程碑'}],
									})(
										<TextArea rows={4} disabled={isEdit} placeholder="请输入" className="w"/>
									)}
								</Form.Item>
							</Col>
						</Col>
						<Col xl={8}>
							<Col xs={24} sm={24} md={18} lg={12} xl={24}>
								<Form.Item label="项目概况" className="w">
									{getFieldDecorator('description', {
										initialValue: fields.description||"",
										rules: [{required: false, message: '请选择项目概况'}],
									})(
										<TextArea rows={7} disabled={isEdit} placeholder="请输入" className="w"/>
									)}
								</Form.Item>
							</Col>
						</Col>
					</Row>
				</Form>
                {itemObj.id==null||itemObj.id==""?null:<MyUpload
                saveAttachment={this.props.saveAttachment}
                delNode={this.props.delNode}
                fileList={attachments}
                pid={attcObj.id}
                projectId={attcObj.projectId}
                disabled={isEdit}
                ossData={this.props.oss}
            />}
			</Card>
		)
	}
}



