import React from 'react';
import {inject, observer} from 'mobx-react';
import {Row, Col, Popover, Icon, Empty, Breadcrumb, Progress, Badge, Affix, Form, message, Modal, Button, Input, notification} from 'antd';

import * as  mobx from 'mobx';
import moment from "moment";
import ItemInfo from "@/components/worktile/ItemInfo";
import ProductDesign from "@/components/product/ProductDesign";
import ProgressList from '@/components/worktile/ProgressList';
import * as utils from "@/utils/utils";

@Form.create()
@inject('ProductStore')
@observer
export default class ProductAdd extends React.Component {
	constructor(props) {
		super(props);
		this.store = this.props.ProductStore;
		this.state = {
            item: {   // 项目信息
                isEdit: false
            },
            design: {  // 产品设计
                isEdit: false
            }
        };
        mobx.autorun(() => {
            this.store.proDesign = this.store.getproDesign;
        });
	}

	componentWillMount() {

	}

	componentDidMount() {
	    this.init();
		// 初始化获取步骤进度信息
		this.store.getStageTree();
		// 获取人员
		this.store.getUserInfoListByRoleCode();
        // 获取大文件上传sts
        this.store.getSts();
		//更新我的任务数
        this.store.getMyTaskCount();
	}

	// 初始化
	init = ()=> {
        this.store.projectId="";
        this.store.projectName="";
        this.store.itemObj = {   // 项目信息
            "id": "",
            "pid": "",
            "projectId": "",
            "treeType": "stage",
            "masters": null,
            "children": [
                {
                    "id": "",
                    "pid": "",
                    "projectId": "",
                    "treeType": "projectinfo",
                    "masters": null,
                    "children": null,
                    "code": null,
                    "name": "",
                    "customer": null,
                    "productType": null,
                    "address": null,
                    "description": null,
                    "milepost": null,
                    "template": 0,
                    "currentTaskState": 0,
                    "currentProgress": 0,
                    "state": 0,
                    "stageId": 0,
                    "projectType":3
                },
                {
                    "id": "",
                    "pid": "",
                    "projectId": "",
                    "treeType": "attc",
                    "masters": null,
                    "children": [],
                    "name": null
                }
            ],
            "stageName": "项目信息",
        };
    }

	// 保存项目信息
	saveProject = (params) => {
		this.store.saveProject(params)
		.then((res) => {
			if (res.id !== null) {
				// 查询树
				message.success("保存成功")
				this.store.projectId = res.id;
				this.store.projectName = res.name;
				// 保存成功，获取树数据
				this.store.getTree(res.id).then((mes) => {
                    this.store.treeData = Object.assign({}, mes); // 前端维护的树
                    // 项目信息 数据
					this.store.itemObj=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="项目信息")[0]);
					// 产品设计 数据
					let proDesign=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="产品设计")[0]);

					// 更新步骤
                    this.store.getStageTree();
                    this.updateStateItem("item", {isEdit: true});

					// 方案设计statgeId
					this.store.proDesign = proDesign;
					this.store.oldStageId = proDesign.id;
					this.store.currentStageId = proDesign.id;
					// 方案设计stage列表
					this.store.getStageList();
                });
            } else {
				message.error("保存失败")
			}
		});
	}

	// updateTree
    updateTree = ()=> {
	    const projectId = this.store.projectId;
        this.store.getTree(projectId).then((mes) => {
            // 项目信息 数据
            this.store.itemObj=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="项目信息")[0]);
            // 产品设计 数据
            let proDesign=Object.assign({}, mes&&mes.children.filter((item)=>item.treeType=="stage"&&item.stageName=="产品设计")[0]);

            this.store.proDesign = proDesign;
        });
    }

	// 保存节点
	saveNodes = (params) => {
		this.store.saveNodes(params).then((res)=>{
           if (res) {
           	   message.success("保存成功")
               // 更新tree
               let treeData = JSON.parse(JSON.stringify(this.store.treeData));
               this.store.treeData = utils.updateTreeData(treeData, "update", res);
               // 更新步骤
               this.store.getStageTree();
               this.store.getMyTaskCount();
           } else {
	           message.error("保存失败")
           }
		});
	}

	// 保存阶段
	saveStage  = (params) => {
		if(params&& params.projectId!=null)this.store.projectId = params['projectId']
		this.store.saveStage(params).then((res)=>{
			if (res) {
				message.success("保存阶段成功")
                this.updateStateItem("design", {isEdit: true});
                // 更新步骤
                this.store.getStageTree();
			} else {
				message.error("保存阶段失败")
			}
		});
	}

	updateState = (params) => {
        this.store.proDesign.masters = Object.assign([], params);
	}

	// 锚点定位
	scrollToAnchor = (anchorName) => {
		if (anchorName) {
			let anchorElement = document.querySelector("#" + anchorName);
			if (anchorElement) {
				anchorElement.scrollIntoView({block: 'center', behavior: 'smooth'});
			}
		}
	}

    // 更新
    updateStateItem = (type, data) =>{
        this.setState({
            [type]: Object.assign({}, data)
        });
    }

    // 保存附件
    saveAttachment = (params) => {
        this.store.saveAttachments(params)
            .then((res)=>{
                if (res) {
                    message.success("附件保存成功")
                    // 更新tree
                    let treeData = JSON.parse(JSON.stringify(this.store.treeData));
                    this.store.treeData = utils.updateTreeData(treeData, "updateChildren", res);
                    // 更新树
                    //this.updateTree();
                    // 更新步骤
                    this.store.getStageTree();
                } else {
                    message.error("附件保存失败")
                }
            });
    }

    // 删除附件
    delNode = (param) => {
        this.store.delNode(param)
            .then((res)=>{
                if (res) {
                    message.success("附件删除成功")
                    // 更新树
                    //this.updateTree();
                    // 更新步骤
                    //this.store.getStageTree();
                } else {
                    message.error("附件删除失败")
                }
            });
    }

	render() {
		const {ossData,comStates, listData, itemObj, proDesign, projectId, projectName} = this.store;
        let isShow = projectId==null||projectId==""?true:false;
		return (
			  <div>
				<div className="con_bottom">
					<Breadcrumb separator=">" style={{margin: '0'}}>
						<Breadcrumb.Item href={"#/bpm/progresslist"}>项目看板</Breadcrumb.Item>
						<Breadcrumb.Item>{projectName||"新增产品设计"}</Breadcrumb.Item>
					</Breadcrumb>
				</div>
                  {/*步骤条*/}
                  <ProgressList
                      listData={mobx.toJS(listData)}
                      store={this.store}
                      scrollToAnchor={this.scrollToAnchor}
                      updateTree={this.updateTree}
                      isShow={isShow}
                  />
				{/*项目信息*/}
				<ItemInfo
					saveProject={this.saveProject}
					itemObj={mobx.toJS(itemObj)}
                    projectId={projectId}
                    projectType={3}
                    updateStateItem={this.updateStateItem}
                    saveAttachment={this.saveAttachment}
                    delNode={this.delNode}
                    updateTree={this.updateTree}
                    isShow={isShow}
                    oss={mobx.toJS(ossData)}
                    store={this.store}
                    {...this.state.item}
				/>
				{/*产品设计*/}
				<ProductDesign
					proDesign={mobx.toJS(proDesign)}
					saveNodes={this.saveNodes}
					saveStage={this.saveStage}
					updateState={this.updateState}
                    store={this.store}
                    updateStateItem={this.updateStateItem}
                    saveAttachment={this.saveAttachment}
                    delNode={this.delNode}
                    oss={mobx.toJS(ossData)}
                    {...this.state.design}

                />

			</div>
		);
	}
}







